import React, { Component } from 'react';
import './use-button.css';

class UseBttn extends Component {

    // constructor(props) {
        // super(props);
        // this.state = {isToggleOn: this.props.status};
        // This binding is necessary to make `this` work in the callback
        // this.SwitchBttnState = this.SwitchBttnState.bind(this);
    // }
    constructor(props) {
        super(props);
        this.state = {
            inProcess: false
        };
    }

    // componentDidMount() {
    //     this.state = {isToggleOn: false};
    // }
    
    SwitchBttnState(e) {

        // console.log("a==" + e.props.status)

        // console.log(e.props.bttnTitle);

        // console.log(e.target.className);

        // console.log(e + !e.props.status);

        // this.props.onSelect(this.props.bttnTitle, this.state.isToggleOn);

        // !this.props.status ? this.setState({isToggleOn: true}) : this.setState({isToggleOn: false});
        // console.log(this.state.to);
        // this.setState(prevState => ({
        //     value: prevState.value + 1
        //   }));
        // this.handleChange(this.props);
        // this.handleChange(this.state);

        // this.props.onSelect(this);

        // this.props.onSelect(this.props.bttnTitle, this.props.status);

        // this.props.testMe(!e.props.status);

        // if (e.props.bttnTitle !== "close") {


        // console.log(e.props.status);
        //     console.log(e.props.bttnTitle);

        // console.log(this.props.changeBttnStatus);
        // if (this.props.changeBttnStatus) {
        //     console.log("correct button");
            // this.props.changeBttnStatus(this.props.bttnTitle);
        // }

        // console.log(!this.props.bttnStatus);

        // console.log(e.props.bttnTitle);


            if (e.props.bttnStatus) {
                // console.log("bttn if")
                // console.log(this.props.bttnStatus);
                this.props.changeBttnStatus(this.props.bttnTitle);

                this.setState({inProcess: ""});

                // this.props.testMe(!e.props.status);
                // console.log(this.props.testMe + " z");
            }
            else {

                // console.log("bttn else")
                // console.log(this.props.bttnStatus);

                // this.props.changeBttnStatus(true);

                this.setState({inProcess: "active inprocess"});

                // this.props.onSelect(this.props.bttnTitle);


                // this.props.bttnStatus(e);

                // console.log(e.props.bttnTitle);

                // buttons "Close" and "Install Eduware Connect" has no states like hosts and participants buttons, so we need to separate them and not change their states
                if (e.props.bttnTitle !== "close" && e.props.bttnTitle !== "Install Eduware Connect") {
                    this.props.changeBttnStatus(this.props.bttnTitle);
                    // I am using an arrow function here, so setTimeout wont carry its this inside the body.
                    setTimeout(() => {
                        this.setState({
                            inProcess: "active"
                        })
                    }, 2000);
                }

                if (e.props.bttnTitle === "close" ) {
                    // console.log(this.props.bttnAction)
                    this.props.bttnAction("none");
                    // this.
                }

            };

        // };



        // console.log(":: " + this.props.bttnTitle)

        // console.log(this.state.isToggleOn);
        // this.setState(prevState => ({
        //   isToggleOn: !prevState.isToggleOn
        // }));
        
      }

    // ButtonAction(target) {
    //     // e.preventDefault();
    //     this.SwitchBttnState();
    //     // console.log('\'' + target + '\' button was clicked');
    // }

    render() {

        // separate buttons into
        // if (this.props.bttnTitle === "close") {
        //         console.log("hoho")
        //     }
        // }
      return (
            // <div className={!this.props.status ? "use-button":"use-button active"} onClick={(e) => this.ButtonAction(this.props.bttnTitle)}>
            // <div className={!this.props.status ? "use-button":"use-button active inprocess"} onClick={(e) => this.SwitchBttnState()}>
            // <div className={"use-button " + this.state.inProcess} onClick={(e) => this.SwitchBttnState(this)}>


            <div className={"use-button " + this.state.inProcess} onClick={(e) => this.SwitchBttnState(this)}>
                <div className="use-bttn-title">
                    {this.props.bttnTitle}
                    {/*<div className={!this.props.status ? "use-button-status none":"use-button-status"}> */}
                    <div className={!this.state.inProcess ? "use-button-status none":"use-button-status"}>{this.state.inProcess === "active inprocess" ? "Connecting..." : "Connected"}</div>
                </div>
            </div>
      );
    }
  }

export default UseBttn;


